@media (max-width: 768px) {
    .event-register-page{
        margin-bottom: 100px;
        padding-top: 60px;
        position: relative;

        .mx-0{
            margin-left: 0;
            margin-right: 0;
        }
        
        .mobile-header {
            display: flex;
            height: 38px;
            padding:0 20px;
            position: absolute;
            top: 20px;
            width: 100vw;
    
            .arrow-back {
                cursor: pointer;
                border-radius: 50%;
                background-color: initial;
                display: inline-block;
                height: 32px;
                padding-left: 5px;
                padding-top: 6px;
                width: 32px;
            }

            .title{
                display: inline-block;
                font-size: 1.125rem;
                font-weight: 400;
                padding-top: 2px;
            }
            
        }

        .event-details{
            padding: 16px;

            .event-reg-card{
                margin: auto;
                max-width: calc(100vw-32px);
                width: calc(100vw-32px);
                border-radius: 12px;
                color: white;
                height: auto;
                position: relative;
                overflow: hidden;
                width: 343px;
                max-width: 343px;
            }
        }


        .event-confirm{
    
        }
    }

}

@media (min-width: 768px) {
    .event-register-page{
        margin-bottom: 100px;
        padding-top: 0;
        position: relative;

        .mx-auto{
            margin-left: auto;
            margin-right: auto;
        }

        .mobile-header {
            display: none;
    
            .arrow-back {
                cursor: pointer;
            }
        }
    
        .event-confirm{
    
        }
    }

}